@import "./constants.scss";

.sidebar-wrapper {
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  margin-left: 80px;
  position: fixed;
  .links {
    padding-top: $padding-from-top-bar;
    .logo-wrapper {
      display: flex;
      align-items: center;
      margin-bottom: 50px;
      .logo-title {
        margin-left: 10px;
      }
    }
  }
  .sidebar-link {
    margin: 10px 0px;
  }
  .sidebar-link:first-child {
    margin-top: 0px;
  }
  .sidebar-link:last-child {
    margin-bottom: 0px;
  }
}
